import app from 'flarum/admin/app';
import withAttr from 'flarum/common/utils/withAttr';
import formatBytes from '../common/formatBytes';
app.initializers.add('sycho-profile-cover', () => {
  app.registry.for('sycho-profile-cover').registerSetting({
    setting: 'sycho-profile-cover.thumbnails',
    type: 'boolean',
    label: app.translator.trans('sycho-profile-cover.admin.thumbnails')
  }).registerSetting(function () {
    const maxSize = this.setting('sycho-profile-cover.max_size', 2048);
    return m("div", {
      className: "Form-group"
    }, m("label", null, app.translator.trans('sycho-profile-cover.admin.max_size')), m("div", {
      className: "ProfileCover-size-input"
    }, m("input", {
      type: "number",
      className: "FormControl",
      value: maxSize(),
      oninput: withAttr('value', maxSize)
    }), m("input", {
      className: "FormControl",
      value: formatBytes(maxSize() * Math.pow(2, 10)),
      disabled: true
    })));
  }).registerPermission({
    icon: 'fas fa-image',
    label: app.translator.trans('sycho-profile-cover.admin.permission.set_cover'),
    permission: 'setProfileCover'
  }, 'start');
});